import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Order from './Order';

export default class Usluga extends Component {
    render() {
        const {value, left} = this.props
        const { title, info, price} = value
        return (
            <div className='row m-3 '>
                <div className={'col-lg-9 border-1px-black p-2 order-' + ~~left}>
                    <div className=' '>
                        <h5 className='mt-0'>
                            {title}</h5>
                        <p className='font_7'>{info}</p>
                    </div>
                </div>
                <Order order={~~!left} price={price}/>
            </div>
        )
    }
}
Usluga.propType = {
    value: PropTypes
        .objectOf(PropTypes.shape({
        title: PropTypes.string.isRequired,
        info: PropTypes.string.isRequired,
        price: PropTypes
            .oneOf([PropTypes.string, PropTypes.number])
            .isRequired
    }).isRequired)
        .isRequired,
    left : PropTypes.bool
}


