import React, {Component} from 'react'
import BlackBlock from '../BlackBlock';
import {NadpisBlue, NadpisWhite} from '../Nadpisi';
import PropTypes from 'prop-types'
import {platform} from '../../utils/platform';
import classNames from 'classnames'

export default class Order extends Component {
    render() {
        const {price, order} = this.props
        const isMobile = platform()

        return (
            <div className={classNames('col-lg-3 order-' + order, {'p-0': isMobile})}>
                <BlackBlock>
                    <a href='tel:+78412306806'>
                        <NadpisBlue>{'от ' + price + ' руб.'}</NadpisBlue>
                        <NadpisWhite>ЗАКАЗАТЬ</NadpisWhite>
                    </a>
                </BlackBlock>
            </div>
        )
    }
}
Order.propType = {
    price: PropTypes.string.isRequired,
    order: PropTypes.number.isRequired
}


