import React, {Component} from 'react'
import classNames from 'classnames'
import {platform} from '../../utils/platform';
import BlackBlock from '../BlackBlock';
import {NadpisBlue, NadpisWhite} from '../Nadpisi';
import PropTypes from 'prop-types'
import Slogan from './Slogan';

export class CustomServiceHead extends Component {
    render() {
        const {about} = this.props
        const isMobile = platform()
        return (
            <div className={classNames('col-lg-8', {'p-0': isMobile})}>
                <div
                    className='p-3 h-100 border-1px-black font_7'
                    dangerouslySetInnerHTML={{
                    __html: about
                }}></div>
            </div>
        )
    }
}
CustomServiceHead.propType = {
    about: PropTypes.string.isRequired
}
/*
*/

export default class CustomService extends Component {
    render() {
        const {items, slogan, about} = this.props.Service

        return (
            <div className='container mt-4'>
                <div className='row ml-3 mr-3'>
                    <Slogan data={slogan}></Slogan>
                    <CustomServiceHead about={about}/>
                </div>
                <CustomServiceBody items={items}/>
            </div>
        )
    }
}

export class CustomServiceBody extends Component {
    render() {
        const isMobile = platform()
        const {items} = this.props
        return (
            <div className='row m-3 '>

                {items.map((val, i) => <Photo key={i} src={val}/>)}

                <div className={classNames('col-lg-4 order-1', {'p-0': isMobile})}>
                    <BlackBlock className='p-0'>
                        <a href='tel:+78412306806'>
                            <NadpisWhite>ЖМИ СЮДА И ЗАКАЖИ</NadpisWhite>
                            <NadpisBlue>
                                Прямо сейчас
                            </NadpisBlue>
                        </a>
                    </BlackBlock>
                </div>
            </div>
        )
    }
}
CustomServiceBody.propType = {
    items: PropTypes
        .arrayOf(PropTypes.string.isRequired,)
        .isRequired
}

class Photo extends Component {
    render() {
        return (
            <div className='col-lg p-0'>
                <div className='h-100 '>
                    <img src={this.props.src} className='img-fluid' alt=''/>
                </div>
            </div>
        )
    }
}
Photo.propType = {
    src: PropTypes.string.isRequired
}



